import { toPDF } from "../pdf";

declare const self: DedicatedWorkerGlobalScope;

self.onmessage = async (evt: MessageEvent<ImageData[]>) => {
  try {
    const images = evt.data;
    // Generate the PDF (Uint8Array) off the main thread
    const pdfUint8 = await toPDF(images);
    // Transfer the underlying buffer back to main thread
    postMessage(pdfUint8.buffer, [pdfUint8.buffer]);
  } catch (err) {
    const message = err instanceof Error ? err.message : String(err);
    postMessage({ error: message });
  }
};
